// material
import { experimentalStyled as styled } from '@material-ui/core/styles';
import InsScOne from 'components/_external-pages/initiatives/InsScOne';
// components
import Page from '../components/Page';
import {
  LandingHero, SecScOne, SecScTwo, 
  
} from '../components/_external-pages/security';

// ----------------------------------------------------------------------

const RootStyle = styled(Page)(({ theme }) => ({
  height: '100%'
}));

const ContentStyle = styled('div')(({ theme }) => ({
  overflow: 'hidden',
  position: 'relative',
  backgroundColor: theme.palette.background.default
}));

// ----------------------------------------------------------------------

export default function Security() {
  return (
    <RootStyle title="Treedefi - Initiatives" id="move_top">
      <LandingHero />
        <ContentStyle>
            <SecScOne />
            <SecScTwo />
        </ContentStyle>
    </RootStyle>
  );
}

